html, body{
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.App{
  background-color: #E8F1F2;
  padding-bottom: 200px;
}

.header{
  height: 255px;
  width: 100%;
  background-color: #13293D;
  display: flex;
}

.header p {
  font-size: 96px;
  color: #ffffff;
  font-weight: 700;
  font-style: bold;
}

.header-container{
  align-self: flex-end;
  height: fit-content;
  margin: 0 211px;
  max-width: max-content;
  margin-bottom: 0;
}

.header p img {
  max-height: 79px;
  transform: translateY(-10px);
}

.main{
  margin: 0 211px;
  min-width: 70%;
  justify-self: auto;
}

.taskList{
  margin: 0 211px;
  min-width: 70%;
  justify-self: auto;
}

.main p{
  margin-top: 0;
  font-size: 48px;
  font-weight: 700;
  color: #13293D;
}

.new-task{
  width: 100%;
  margin-top: 40px;
}

.title{
  width: fit-content;
  padding: 8px 16px;
  background-color: #13293D;
  border-radius: 8px;
  color: #ffffff;
  font-weight: 600;
  font-size: 24px;
}


form{
  margin-top: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-auto-rows: minmax(0px, auto);
  justify-items: stretch;
  align-items: stretch;
  column-gap: 1.5em;
  justify-content: space-between;
}


input{
  padding: 20px 15px;
  border: none;
  background-color: #ffffff;
  grid-row: 2;
}


.task-name{
  grid-column: 1/8;
}

.task-date{
  grid-column: 8/11;
}

label{
  font-size: 24px;
  color: #414141;
  font-weight: 600;
  margin-bottom: 8px;
  grid-row: 1;
}

form button{
  border: none;
  background-color: #ffffff;
  color: #616161;
  font-weight: 700;
  grid-column: 11/13;
  transform: translateY(42px);
  max-height: 39px;
  min-height: 39px;
}

hr{
  margin-top: 40px;
  border-top: 4px solid #13293D;
}

@media (min-width:961px){
  span.add-icon {display: inline-block;}
  span.add-text {display: inline-block;}
  
}

@media (max-width:960px){
  span.add-icon {display: inline-block;}
  span.add-text {display: none;}
  .header-container,
  .main, .taskList {
    margin: 0 auto;
    margin-top: 50px;
    width: 80%;
    transform: translateY(-50px);
  }
  .header p {
    font-size: 64px;
    font-style: bold;
  }
  .header p img {
    max-height: 64px;
    transform: translateY(-10px);
  }
  .label{font-size: 16px;}
  .task-name{grid-column: 1/7;}
  .task-date{grid-column: 7/11;}
}