.list {
    position: relative;
    margin: 0 211px;
    min-width: 70%;
    justify-self: auto;
    background-color: #ffffff;
    margin-top: 33px;
    height: 68px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.list img{
    position: absolute;
    right: 20px;
    cursor: pointer;
}

.list h3{
    padding: 20px;
    color: #000000;
}

@media (max-width:960px){
    .list {
      margin: 0 auto;
      width: 80%;
      margin-top: 33px;
      transform: translateY(-50px);
    }
}